.App {
  font-family: sans-serif;
  text-align: center;
}

.stopwatch-container {
  max-width: 300px;
  margin: 0 auto;
  text-align: center;
}

.stopwatch-title {
  font-size: 1.5rem;
  margin: 10px 0;
}

.stopwatch-display {
  font-size: 2rem;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
}

.stopwatch-controls {
  margin: 20px 0;
  display: flex;
  justify-content: center;
}

.start-stop,
.lap,
.reset {
  padding: 10px 20px;
  font-size: 1rem;
  margin: 0 10px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
}

.start-stop:hover,
.lap:hover,
.reset:hover {
  background-color: #0056b3;
}

.lap-list {
  text-align: left;
}

.lap-list ul {
  list-style: none;
  padding: 0;
}

.lap-list ul li {
  font-size: 1rem;
  margin: 5px 0;
}
